.a2 {
  text-decoration: none;
  text-align: center;
  color: #FFFFFF;
  font-size: 20px;
  font-weight: bold;
  border: none;
  position: absolute;
  right: 15%;
  top: 55%;
}

.a2:before {
  content: "";
  position: absolute;
  width: 100%;
  height: 2px;
  bottom: -5px;
  left: 0;
  background-color: #FFFFFF;
  visibility: hidden;
  -webkit-transform: scaleX(0);
  transform: scaleX(0);
  transition: all 0.3s ease-in-out 0s;
}

.a2:hover:before {
  visibility: visible;
  -webkit-transform: scaleX(1);
  transform: scaleX(1);
}

